<style media="screen">
    .productColorImg span.n-right{
        position: absolute;
        top: -5px !important;
    }
</style>
<template>
    <span class="productColorImg" @click="imgIndex.set_img_index(id)" id="Apickfiles{{id}}">
        <input v-if="listobjselecteds==true" data-rule="required" v-bind:value="imgsrc" class="form-control hidden" type="text" name="{{'productColorImg'+id}}" placeholder="">
        <img v-if="imgsrc!=''" style="max-width:70px; max-height:70px;" v-bind:src="imgsrc+'?imageView2/1/w/70/h/70'" alt="" />
        <button v-if="imgsrc==''" class="btn btn-success">
            <span></span>
        </button>
    </span>
</template>

<script type="text/javascript">
    import {productSalesAttribute,imgIndex} from '../../model'
    export default{
        props:['id','imgsrc','listobjselecteds'],
        data(){
            return{
                imgIndex:imgIndex
            }
        }
    }
</script>



